    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(25%, rgba(255, 255, 255, 0.67)), color-stop(67%, rgba(255, 255, 255, 0.33)), color-stop(90%, rgba(255, 255, 255, 0.03)), to(rgba(255, 255, 255, 0)));
