    -webkit-transform: rotateZ(50deg) translate(0, 200%);
    transform: rotateZ(50deg) translate(0, 200%);
    -webkit-animation: sheen 1.67s linear infinite;
    animation: sheen 1.67s linear infinite;
    pointer-events: none
}

.attention:active:after {
    opacity: 0
