.keyboard {
    text-align: center;
    font-size: 0
}

.keyboard>.keyboard-line {
    white-space: nowrap
}

.keyboard button,
.keyboard .button-spacer {
    font-size: 24px;
    text-align: center;
    padding: 0;
    line-height: 56px;
    margin: 5px;
    width: 56px;
    height: 56px;
    min-width: initial
}

.keyboard button {
    font-family: Helvetica, Arial, sans-serif;
    text-transform: uppercase;
    background-color: #6ea6d1
}

.keyboard button:hover {
    background-color: #95bedd
}

.keyboard button:active {
    background-color: #478ec5
}

.keyboard button.highlight {
    background-color: #ff6f00
}

.keyboard button.highlight.anim {
    animation: keyboard-colorchange 1s 4;
    -webkit-animation: keyboard-colorchange 1s 4
}

.keyboard button.highlight:hover {
    background-color: #ff8c33
}

.keyboard button.highlight:active {
    background-color: #cc5900
}

.keyboard.lowercase button,
.keyboard .lowercase button {
    text-transform: lowercase
}

.keyboard .disabled,
.keyboard.disabled button {
    pointer-events: none
}

.keyboard .button-spacer {
    display: inline-block
}

@keyframes keyboard-colorchange {
    0% {
        background-color: #ff6f00
    }
    30% {
        background-color: #6ea6d1
    }
    32% {
        background-color: #6ea6d1
    }
    70% {
        background-color: #ff6f00
    }
    100% {
        background-color: #ff6f00
    }
}

@-webkit-keyframes keyboard-colorchange {
    0% {
        background-color: #ff6f00
    }
    30% {
        background-color: #6ea6d1
    }
    32% {
        background-color: #6ea6d1
    }
    70% {
        background-color: #ff6f00
    }
    100% {
        background-color: #ff6f00
    }
}

@media (max-width: 767px) {
    .keyboard {
        margin-left: -1rem;
        margin-right: -1rem;
        padding: 0 1%
    }
    .keyboard button,
    .keyboard .button-spacer {
        font-size: 20px;
        line-height: 44px;
        width: initial;
        height: 44px;
        min-width: 8.6%;
        margin: 4px .6%
    }
}

@media (max-width: 424px) {
    .keyboard button,
    .keyboard .button-spacer {
        font-size: 18px;
        line-height: 40px;
        height: 40px;
        margin-top: 3px;
        margin-bottom: 3px
    }
