.correct-formation .submit-check {
    margin-top: .5em
}

@media (max-width: 767.98px) {
    .correct-formation .row>div {
        padding-top: 2em;
        padding-bottom: 2em;
        border-bottom: 1px dashed silver
    }
    .correct-formation .row>div:first-child {
        padding-top: 0
    }
    .correct-formation .row>div:last-child {
        padding-bottom: 0;
        border: none
    }
}

.correct-formation .option-container,
.compare-items .option-container {
    border: 2px solid silver;
    border-radius: 6px;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    line-height: 0;
    max-width: 250px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("https://k2l.bndry.co.uk/basicskills/video/formation/letters/let-bother.png");
    margin-left: auto;
    margin-right: auto
}

.correct-formation .option-container>div,
.compare-items .option-container>div {
    position: relative;
    padding-bottom: 100%
}

.correct-formation .option-container>div>.play,
.correct-formation .option-container>div .video-wrapper,
.compare-items .option-container>div>.play,
.compare-items .option-container>div .video-wrapper {
    position: absolute;
    width: 100%;
    height: 100%
}

.correct-formation .option-container>div .gifv-wrapper,
.compare-items .option-container>div .gifv-wrapper {
    height: 100%
}

.correct-formation .option-container>div>.play .video-play-button,
.compare-items .option-container>div>.play .video-play-button {
    position: absolute;
    width: 80px;
    height: 80px;
    left: 50%;
    top: 50%;
    margin-top: -40px;
    margin-left: -40px
}

.racecar {
    display: inline-block;
    width: 100%;
    max-width: 300px;
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-white.png?v4");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: contain;
    vertical-align: middle;
    width: 300px;
    height: 90px;
    margin: 18.75px 25px
}

.racecar>svg {
    width: 100%;
    height: auto
}

.racecar>svg text {
    font-family: 'Helvetica-Bold', 'Helvetica', 'Arial-bold', 'Arial', 'sans-serif';
    font-weight: 700;
    font-size: 46px;
    fill: black
}

.racecar .number {
    font-family: 'Helvetica-Bold', 'Helvetica', 'Arial-bold', 'Arial', 'sans-serif';
    font-weight: 700;
    color: black;
    display: inline-block;
    width: 100%;
    text-align: center
}

.racecar.red>span,
.racecar.black>span {
    color: white
}

.racecar.red text,
.racecar.black text {
    fill: white
}

.racecar.white {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-white.png?v4")
}

.racecar.red {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-red.png?v4")
}

.racecar.green {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-green.png?v4")
}

.racecar.blue {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-blue.png?v4")
}

.racecar.black {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-black.png?v4")
}

.racecar.orange {
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/racecars/racecar-orange.png")
