.seesaw-container {
    text-align: center;
    overflow: hidden;
    padding-top: 120px;
    max-width: 500px;
    margin: 0 auto
}

.seesaw-container .item {
    color: black;
    text-align: center;
    font-family: "ABeeZee", "Roboto", Helvetica, Arial, sans-serif;
    font-weight: bold;
    font-size: 1.6em;
    background: #73d7f5;
    padding: 1em .5em;
    border-radius: 8px;
    -webkit-box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.25);
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.25);
    -webkit-transition: -webkit-transform .25s;
    transition: -webkit-transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
    cursor: pointer
}

@media (max-width: 767.98px) {
    .seesaw-container .item {
        font-size: 1.2em
    }
}

@media (max-width: 543.98px) {
    .seesaw-container .item {
        font-size: 1em
    }
}

