.seesaw-container .bar {
    border-bottom: 16px solid #b61004;
    -webkit-transition: -webkit-transform .33s;
    transition: -webkit-transform .33s;
    transition: transform .33s;
    transition: transform .33s, -webkit-transform .33s;
    -webkit-transition-timing-function: ease;
    transition-timing-function: ease;
    -webkit-transform-origin: center bottom;
    transform-origin: center bottom
}

.seesaw-container .bar.tilt-right {
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg)
}

.seesaw-container .bar.tilt-left {
    -webkit-transform: rotate(-30deg);
    transform: rotate(-30deg)
}

.seesaw-container .bar .bar-middle {
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap
}

.seesaw-container .bar .center-blocker {
    height: 16px;
    width: 10px;
    background: #b61004;
    border-radius: 10px 10px 0 0;
    margin-bottom: -1px
}

.seesaw-container .bar .center-blocker:first-child {
    float: left
}

.seesaw-container .bar .center-blocker:last-child {
    float: right
}

.seesaw-container .bar.tilt-left .item,
.seesaw-container .bar.tilt-right .item {
    cursor: default;
    pointer-events: none
}

.seesaw-container .bar.tilt-left .item:hover,
.seesaw-container .bar.tilt-right .item:hover {
    background: #73d7f5
}

