@-webkit-keyframes seesaw-item-fall-right {
    from {
        -webkit-transform: translateY(0) translateX(0);
        transform: translateY(0) translateX(0)
    }
    40% {
        -webkit-transform: translateY(30px) translateX(40px) rotate(20deg);
        transform: translateY(30px) translateX(40px) rotate(20deg)
    }
    100% {
        -webkit-transform: translateY(120px) translateX(100px) rotate(-50deg);
        transform: translateY(120px) translateX(100px) rotate(-50deg);
        display: none
    }
}

