.body-customisation {
    text-align: center
}

.body-customisation h3 {
    font-family: "ABeeZee", "Roboto", Helvetica, Arial, sans-serif
}

.body-customisation h3:after {
    content: '';
    display: inline-block;
    width: .8em;
    height: .8em;
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/icons/fa-check-green.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    vertical-align: middle;
    margin-left: .25em
}

.body-customisation .option {
    text-align: center;
    font-weight: bold;
    display: inline-block;
    width: 100px;
    min-height: 100px;
    margin: 5px;
    padding: 6px;
    border: 1px solid silver;
    border-radius: 8px;
    cursor: pointer
}

.body-customisation .option:hover {
    background: rgba(0, 0, 0, 0.075)
}

.body-customisation .option>img {
    display: block;
    max-width: 100%;
    height: auto
}

.body-customisation .option>img.has-bg {
    border: 1px solid silver;
    border-radius: 4px
}

.sentence-selects-container {
    font-size: 1.5em;
    text-align: center
}

.sentence-selects-container .sentence-selects {
    border-radius: 12px;
    padding: 8px;
    display: inline-block;
    background: rgba(255, 255, 255, 0.5);
    margin: 2em auto 0
}

.sentence-selects-container .select-wrapper {
    padding: 4px;
    display: inline-block;
    vertical-align: baseline;
    padding: 4px
}

.sentence-selects-container .select-wrapper>span {
    color: #333
}

.sentence-selects-container .select-wrapper>span.start {
    padding-left: .5em
}

.sentence-selects-container .select-wrapper>span.end {
    padding-right: .5em
}

.sentence-selects-container .select-wrapper.submit {
    vertical-align: bottom
}

.sentence-selects-container select,
.sentence-selects-container button {
    text-align: left;
    height: 50px;
    min-width: 50px;
    margin: 0
}

.sentence-selects-container select {
    padding: .2em;
    display: block;
    color: #333;
    line-height: 1.5;
    padding: .3em 1.6em .3em .4em;
    width: 100%;
    max-width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    border: 2px solid #ff6f00;
    -webkit-box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    border-radius: .3em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/icons/dropdown-arrow-brand-primary.svg");
    background-repeat: no-repeat, repeat;
    background-position: right .4em top 50%, 0 0;
    background-size: .65em auto, 100%
}

.sentence-selects-container select::-ms-expand {
    display: none
}

.sentence-selects-container select:hover {
    border-color: #ffa966
}

.sentence-selects-container select:focus {
    -webkit-box-shadow: 0 0 1px 3px #73d7f5;
    box-shadow: 0 0 1px 3px #73d7f5;
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none
}

.sentence-selects-container select option {
    font-weight: normal
}

.sentence-selects-container .answers {
    text-align: left;
    background: white;
    border-radius: 12px;
    padding: 1em;
    margin: 2em auto 0;
    max-width: 640px
}

.sentence-selects-container .answers .count {
    font-weight: bold;
    text-align: right
}

.sentence-selects-container .answers .count>span {
    color: white;
    text-align: center;
    display: inline-block;
    width: 1.75em;
    height: 1.75em;
    font-size: 1.3em;
    line-height: 1.75em;
    background: #73d7f5;
    border-radius: 50%
}

.sentence-selects-container .answers>ul {
    margin: 0
}

.sentence-selects-container .answers>ul>li {
    margin-bottom: .5em
}

.sentence-selects-container .answers>ul>li:after {
    content: '';
    display: inline-block;
    vertical-align: bottom;
    background-image: url("https://k2l.bndry.co.uk/basicskills/img/icons/fa-check-green.svg");
    background-size: contain;
    background-repeat: no-repeat;
    width: 1em;
    height: 1em;
    margin-left: .33em
}

